.courses-page {
  background-color: var(--bg-color); /* From style.css */
  min-height: 100vh;
  color: var(--text-color);
  font-family: 'Poppins', sans-serif;
  padding-bottom: 50px;
}

/* Navbar */
.course-navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 40px;
  background: rgba(10, 10, 10, 0.95);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  position: sticky;
  top: 0;
  z-index: 100;
}

.back-link {
  color: var(--text-secondary);
  text-decoration: none;
  font-size: 0.9rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
  padding: 8px 15px;
  border-radius: 20px;
  transition: 0.3s;
}

.back-link:hover {
  border-color: var(--primary-color);
  color: var(--primary-color);
}

.login-btn {
  background: transparent;
  border: 1px solid var(--primary-color);
  color: var(--primary-color);
  padding: 8px 25px;
  border-radius: 5px;
  cursor: pointer;
  font-weight: 600;
  transition: 0.3s;
}

.login-btn:hover {
  background: var(--primary-color);
  color: var(--bg-color);
}

/* Header */
.courses-header {
  text-align: center;
  padding: 60px 20px;
}

.courses-header h1 {
  font-family: 'Montserrat', sans-serif;
  font-size: 3rem;
  margin-bottom: 10px;
}

.highlight { color: var(--primary-color); }

.courses-header p { color: var(--text-secondary); }

/* Grid */
.courses-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 30px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

/* Card */
.course-card {
  background: var(--light-bg);
  border-radius: 15px;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.05);
  transition: 0.3s;
}

.course-card:hover {
  transform: translateY(-5px);
  border-color: var(--primary-color);
  box-shadow: 0 10px 30px rgba(0,0,0,0.5);
}

.course-thumb {
  position: relative;
  height: 200px;
}

.course-thumb img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.price-tag {
  position: absolute;
  top: 15px;
  right: 15px;
  background: var(--primary-color);
  color: var(--bg-color);
  padding: 5px 10px;
  border-radius: 5px;
  font-weight: bold;
  font-size: 0.9rem;
}

.course-info { padding: 25px; }

.course-info h3 { 
  font-family: 'Montserrat', sans-serif; 
  margin-bottom: 10px;
  color: var(--text-color);
}

.course-desc {
  font-size: 0.9rem;
  color: var(--text-secondary);
  margin-bottom: 20px;
}

/* Lesson List */
.lesson-list {
  background: rgba(0,0,0,0.3);
  padding: 15px;
  border-radius: 10px;
  margin-bottom: 20px;
}

.lesson-list h4 {
  font-size: 0.9rem;
  color: var(--text-secondary);
  margin-bottom: 10px;
  text-transform: uppercase;
}

.lesson-list ul {
  list-style: none;
  padding: 0;
}

.lesson-list li {
  display: flex;
  align-items: center;
  padding: 8px 0;
  border-bottom: 1px solid rgba(255,255,255,0.05);
  font-size: 0.9rem;
}

.lesson-list li:last-child { border-bottom: none; }

.lesson-list li.locked { color: var(--text-tertiary); }
.lesson-list li.unlocked { color: var(--text-color); }

.icon { margin-right: 10px; font-size: 0.8rem; }

.play-demo-btn {
  margin-left: auto;
  font-size: 0.7rem;
  padding: 3px 8px;
  background: rgba(255, 193, 7, 0.2);
  color: var(--primary-color);
  border: 1px solid var(--primary-color);
  border-radius: 4px;
  cursor: pointer;
}

.play-demo-btn:hover {
  background: var(--primary-color);
  color: var(--bg-color);
}

/* Actions */
.enroll-btn {
  width: 100%;
  padding: 12px;
  background: var(--primary-color);
  color: var(--bg-color);
  border: none;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.3s;
}

.enroll-btn:hover {
  background: #f31212; /* Lighter yellow */
  color:rgb(255, 255, 255);
}